.work-block {
  background-color: #181818;
  position: relative;
  &__container {
    position: relative;
    z-index: 2;
    border-top: 3px solid #7e8084;
    border-bottom: 3px solid #7e8084;
  }
  &__items {
    display: flex;
    justify-content: space-between;
    margin: 0 -5px;
  }
  &__filter {
    max-width: 750px;
    margin: 0 auto;
  }
  &__search {
    margin-top: 30px;
  }
  &__searched {
    padding: 10px 20px;
    text-align: center;
    border: 1px dashed $main-color;
    border-radius: 4px;
    margin-top: 15px;
    font-size: 24px;
    color: #7e8084;
  }
}
.work-block-item {
  width: 100%;
  max-width: 175px;
  position: relative;
  padding: 0 5px;
  &:before,
  &:after {
    content: '\f005';
    font-family: 'Font Awesome 5 Free';
    font-weight: 900;
    line-height: 1;
    color: $main-color;
    font-size: 28px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }
  &:before {
    left: -67px;
  }
  &:after {
    right: -67px;
  }
  &:not(:last-child) {
    &:after {
      display: none;
    }
  }
  &__content {
    padding: 10px;
    &:last-child {
      padding-top: 0;
    }
  }
  &__count {
    padding: 10px 0 15px 0;
    background: url('../img/backgrounds/brand-background.png') no-repeat center center;
    background-size: contain;
    span {
      display: block;
      text-align: center;
      &:first-child {
        font-size: 18px;
        font-weight: 700;
        line-height: 1;
      }
      &:last-child {
        font-size: 14px;
      }
    }
  }
  .input {
    display: none;
  }
  .label {
    display: block;
    background-color: #151314;
    border-radius: 4px;
    border: 1px solid $main-color;
    color: #7e8084;
    transition: 0.3s;
    &.checked {
      color: #151314;
      background-color: #fff;
      .work-block-item__icon {
        color: #151314;
      }
    }
  }
  &__title {
    display: block;
    text-align: center;
    font-size: 14px;
    text-transform: uppercase;
    white-space: nowrap;
    margin-bottom: 7px;
  }
  &__icon {
    font-size: 40px;
    display: block;
    text-align: center;
    color: #fff;
    transition: 0.3s;
  }
}

@media all and(max-width: 768px) {
  .work-block-item:after, .work-block-item:before {
    display: none;
  }
}
@media all and(max-width: 576px) {
  .work-block-item__title {
    font-size: 12px;
  }
}
@media all and(max-width: 480px) {
  .work-block-item__count {
    background: 0;
    padding-bottom: 5px;
  }
  .work-block-item__title {
    font-size: 12px;
  }
}
@media all and(max-width: 375px) {
  .work-block-item__title {
    font-size: 9px;
  }
  .work-block-item__icon {
    font-size: 34px;
  }
  .work-block__searched {
    font-size: 20px;
  }
  .work-block-item__count span:last-child {
    font-size: 12px;
  }
  .work-block-item__content {
    padding-right: 6px;
    padding-left: 6px;
  }
}